<template>
  <div class="category">
    <!-- 搜索框 -->
    <SearchInput></SearchInput>

    <div class="contain_wrap">
      <!-- 侧边导航栏 开始 -->
      <van-list class="left-class">
        <van-cell @click="exClass(index1)" v-for="(item1,index1) in leftClassData" :key="index1">
          <router-link class="navigator" :class="index1==currentIndex?'active':''" to>{{item1}}</router-link>
        </van-cell>
      </van-list>
      <!-- 侧边导航栏 结束 -->
      <!-- 右边商品栏 开始 -->
      <van-list class="right-class">
        <van-cell
          class="right-item"
          v-for="(item2,index2) in rightProductData[currentIndex]"
          :key="index2"
        >
          <div class="name_title">
            <p class="partition">/</p>
            <p>{{item2.cat_name}}</p>
            <p class="partition">/</p>
          </div>
          <div class="contain">
            <router-link
              class="navigator"
              :to="'/goodslist?cid='+item3.cat_id"
              v-for="(item3,index3) in item2.children"
              :key="index3"
            >
              <van-image
                :src="item3.cat_icon?item3.cat_icon:'https://ww1.sinaimg.cn/large/007rAy9hgy1g24by9t530j30i20i2glm.jpg'"
              ></van-image>
              <p>{{item3.cat_name}}</p>
            </router-link>
          </div>
        </van-cell>
      </van-list>
      <!-- 右边商品栏 结束 -->
    </div>

    <FootTab></FootTab>
  </div>
</template>

<script>
import { reaxios } from "@/assets/js/reaxios";
import "@/assets/css/category.css";
import SearchInput from "../components/SearchInput";
import FootTab from "../components/FootTab";
export default {
  data() {
    return {
      categories: [],
      // 左侧分类数据
      leftClassData: [],

      // 右侧商品数据
      rightProductData: [],

      // 当前点击选项索引值
      currentIndex: 0,
      // 右侧距离顶部距离
      scrollTop: 0
    };
  },
  created() {
    this.getData();
  },
  methods: {
    /* 获取数据 */
    getData() {
      // 分类总数据
      reaxios(0, { url: "/categories" }).then(result => {
        this.categories = result.data.message;
        this.categories.forEach(v => {
          this.leftClassData.push(v.cat_name);
          this.rightProductData.push(v.children);
        });
      });
    },
    // 左侧分类切换
    exClass(index) {
      this.currentIndex = index;
      this.scrollTop = 0;
    }
  },

  // 自定义组件注册
  components: { SearchInput, FootTab }
};
</script>

<style lang="less" scoped>
.contain_wrap {
  display: flex;
  padding-top: 50px;
  .left-class {
    flex: 2;
    .navigator {
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 15px;
      text-align: center;
      color: #000;
    }

    .active {
      color: var(--themeColor);
      background-color: var(--threeColor);
      opacity: 0.5;
      border-bottom: 1px solid #f7e4d7;
    }
  }
  .right-class {
    flex: 5;
    .right-item {
      .name_title {
        display: flex;
        height: 40px;
        justify-content: center;
        align-items: center;
        color: var(--secondColor);

        .partition {
          padding: 0 5px;
          color: var(--threeColor);
        }
      }
      /* flex-wrap 拆行属性 */
      .contain {
        display: flex;
        flex-wrap: wrap;
        .navigator {
          width: 33.33%;
          text-align: center;
          color: #000;

          .van-image {
            width: 50%;
          }
        }
      }
    }
  }
}
</style>


